body {
  margin: 0 0 500px;
  background-color: #ede3d9;
  background-image: url(../img/bg-body.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}
img {
  max-width: 100%;
}
.container {
  overflow: hidden;
}
.block {
  display: block;
}
.header {
  padding-top: 30px;
  padding-bottom: 20px;
  text-align: center;
  padding-left: 15px;
  padding-right: 15px;
}
.header__title {
  margin: 0;
  font-weight: 700;
  font-size: 19px;
  line-height: 1.4;
  font-family: "Maitree", serif;
  color: #915110;
  font-style: italic;
}
.header__logo {
  display: block;
  margin: 0 auto;
  width: 140px;
}
.first {
  padding-right: 20px;
  padding-bottom: 50px;
  padding-left: 20px;
}
.first__title {
  max-width: 380px;
  margin: 0 auto 14px;
  padding: 20px 15px;
  font-weight: bold;
  font-size: 18px;
  line-height: 1.2;
  font-family: "Maitree", serif;
  text-align: center;
  color: #ffffff;
  font-style: italic;
  background: #fff601;
  background: linear-gradient(180deg, #fff601, #c76f16);
  border-width: 2px;
  border-style: dashed;
  border-color: #ffffff;
  border-radius: 214px;
  transform: rotate(-10deg);
  text-decoration-line: underline;
}
.first__block-img {
  position: relative;
}
.first__block-img::before {
  content: '';
  position: absolute;
  bottom: 18px;
  left: -10px;
  width: 104px;
  height: 115px;
  background-image: url(../img/vitamin-e.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transform: scale(-1, 1);
}
@media (max-width: 990px) {
  .first__block-img::after {
    content: "";
    position: absolute;
    right: -98px;
    bottom: -139px;
    z-index: -1;
    width: 198px;
    height: 324px;
    background-image: url(../img/leaf.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    transform: rotate(-135deg);
  }
}
.first__img-1 {
  display: block;
  box-sizing: border-box;
  width: 170px;
  margin: 0 auto;
  border: 4px solid #fff;
  transform: rotate(-3deg);
}
.first__img-2 {
  position: absolute;
  top: -5px;
  left: 50%;
  width: 98px;
  border: 4px solid #fff;
  transform: translateX(50%);
}
.first-list {
  width: fit-content;
  margin: 0 auto 70px;
  padding-right: 15px;
  padding-left: 70px;
  list-style-type: none;
}
.first-item {
  position: relative;
  display: flex;
  margin-bottom: 10px;
  padding: 5px 15px 0;
  font-weight: bold;
  font-size: 11px;
  line-height: 1.6;
  font-family: "Maitree", serif;
  text-align: left;
  color: #915110;
  font-style: italic;
  background: #fdfbfb;
  background: linear-gradient(180deg, #fdfbfb, #eaedee);
  border-radius: 0px 94px 94px;
}
.first-item::before {
  content: '';
  display: block;
  width: 25px;
  height: 25px;
  margin-top: -5px;
  margin-right: 10px;
  background-position: center;
  border-width: 2px;
  border-style: solid;
  border-color: #e4e4e4;
  border-radius: 50%;
}
.first-item.one::before {
  background-image: url(../img/first-item-one.png);
}
.first-item.two::before {
  background-image: url(../img/first-item-two.png);
}
.first-item.three::after {
  content: '';
  position: absolute;
  top: 7px;
  left: 22px;
  width: 15px;
  height: 15px;
  background-color: #be0027;
  border-radius: 50%;
}
.listing__list {
  margin: 0;
  padding-left: 40px;
  list-style-type: none;
  font-weight: bold;
  font-size: 14px;
  line-height: 1.6;
  font-family: "Maitree";
  color: #915110;
  font-style: italic;
}
.listing__list:not(:last-child) {
  margin-bottom: 20px;
}
.listing__list:first-child::before {
  content: '';
  position: absolute;
  bottom: -47px;
  left: -20px;
  display: block;
  width: 89px;
  height: 75px;
  background-image: url(../img/green-arrow.svg);
}
.listing__list:nth-child(2) {
  color: #db5201;
}
.listing__wrap {
  position: relative;
  padding-top: 70px;
}
.listing__wrap::before {
  content: '';
  position: absolute;
  top: 0;
  right: 10px;
  width: 100px;
  height: 100px;
  background-image: url(../img/natural-label.png);
  background-size: contain;
}
.listing__wrap::after {
  content: '';
  position: absolute;
  top: 97px;
  left: 10px;
  width: 80px;
  height: 80px;
  background-image: url(../img/office-product-label.png);
  background-size: contain;
}
.volume {
  width: fit-content;
  margin: 0 auto 29px;
}
.volume p {
  position: relative;
  width: fit-content;
  padding: 10px 10px;
  font-size: 13px;
  line-height: 1.6;
  font-family: "Open Sans", sans-serif;
  color: #000000;
  background-color: rgba(255, 255, 255, 0.86);
}
.volume p:not(:last-child) {
  margin-bottom: 18px;
}
.volume p::before {
  content: '';
  position: absolute;
  bottom: -7px;
  left: -7px;
  z-index: -1;
  width: calc(100% + 7px);
  height: calc(100% + 7px);
  background-color: rgba(145, 81, 16, 0.35);
}
.form__discount {
  position: relative;
  box-sizing: border-box;
  max-width: 180px;
  margin: 0 auto 50px;
  padding: 12px 10px 25px;
  font-weight: bold;
  font-size: 17px;
  line-height: 1.2;
  font-family: "Maitree", sans-serif;
  text-align: center;
  color: #ff0e01;
  font-style: italic;
  background-color: #ffffff;
  border-width: 3px;
  border-style: solid;
  border-color: #db5201;
  border-radius: 0px 28px 28px;
}
.form__discount p {
  margin: 0;
}
.form__discount::after {
  content: '';
  display: block;
  width: 140px;
  height: 55px;
  margin: 0 auto;
  background-image: url(../img/discount.gif);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
.form__discount::before {
  content: '';
  position: absolute;
  bottom: -65px;
  left: 50%;
  display: block;
  width: 95px;
  height: 95px;
  margin: 0 auto;
  background-image: url(../img/down-arrow.gif);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transform: translateX(-50%);
}
.form__spin {
  position: relative;
  padding: 15px;
}
